@import"https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap";
@import"https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";
@import"https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";
@import"https://fonts.googleapis.com/css2?family=Sofia&display=swap";

* {
 margin: 0;
 padding: 0;
 box-sizing: border-box
}

* img {
 background-color: #fff !important
}

body {
 font-size: 62.5%
}

::selection {
 background-color: rgba(66, 134, 244, .5333333333) !important
}

::selection img {
 background-color: rgba(0, 0, 0, 0)
}

html {
 font-size: 1.6rem
}

.sm-screen {
 display: none
}

.button-17 {
 align-items: center;
 appearance: none;
 border-radius: 24px;
 border-style: none;
 box-sizing: border-box;
 font-family: "Poppins", sans-serif;
 cursor: pointer;
 display: inline-flex;
 fill: currentcolor;
 font-size: 12px;
 font-weight: 500;
 height: 30px;
 justify-content: center;
 letter-spacing: .25px;
 line-height: normal;
 max-width: 100%;
 overflow: visible;
 padding: 2px 24px;
 position: relative;
 text-align: center;
 text-transform: none;
 transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1), opacity 15ms linear 30ms, transform 270ms cubic-bezier(0, 0, 0.2, 1) 0ms;
 user-select: none;
 -webkit-user-select: none;
 touch-action: manipulation;
 width: 130px;
 will-change: transform, opacity;
 z-index: 0;
 background: #f6f9fe
}

.button-17:hover {
 background-color: #000;
 color: #fff;
 box-shadow: rgba(3, 102, 214, .3) 0px 0px 0px 3px
}

.button-17:active {
 box-shadow: rgba(3, 102, 214, .3) 0px 0px 0px 3px;
 outline: none
}

.button-17:focus {
 outline: none;
 border: 2px solid #4285f4
}

.button-17:not(:disabled) {
 box-shadow: rgba(3, 102, 214, .3) 0px 0px 0px 3px
}

.button-17:not(:disabled):hover {
 box-shadow: rgba(3, 102, 214, .3) 0px 0px 0px 3px
}

.button-17:not(:disabled):focus {
 box-shadow: rgba(3, 102, 214, .3) 0px 0px 0px 3px
}

.button-17:not(:disabled):active {
 box-shadow: rgba(3, 102, 214, .3) 0px 0px 0px 3px
}

.button-17:disabled {
 box-shadow: rgba(3, 102, 214, .3) 0px 0px 0px 3px
}

a {
 text-decoration: none;
 color: #fff
}

li {
 list-style-type: none
}

img {
 width: 100%
}

.section-title {
 font-size: 2.5rem;
 font-family: "Poppins", sans-serif;
 margin-bottom: 5vh
}

center {
 width: 100%;
 text-align: center !important
}

img {
 animation: skeleton-loading 1s linear infinite alternate
}

@keyframes skeleton-loading {
 0% {
  background-color: #a3b8c2
 }

 100% {
  background-color: #f0f3f5
 }
}

p {
 font-family: "Raleway", sans-serif
}

.main-error {
 background: url(https://cdn.svgator.com/images/2022/01/funny-404-error-page-design.gif) no-repeat center;
 background-color: #fff;
 background-size: contain;
 height: 100vh;
 width: 100vw;
 display: grid;
 align-items: center;
 justify-content: center
}

.main-error a {
 margin: 0 auto
}

.main-error h1 {
 color: #3c4043;
 font-family: "Poppins", sans-serif
}

.main-error::selection {
 background: rgba(0, 0, 0, 0)
}

.main-header {
 display: flex;
 align-items: center;
 justify-content: space-around;
 height: 10vh;
 box-shadow: rgba(0, 0, 0, .16) 0px 1px 4px, #333 0px 0px 0px 3px;
 position: sticky;
 top: 0px;
 background-color: rgba(255, 255, 255, .642);
 z-index: 99
}

.main-header__logo {
 font-family: "Poppins", sans-serif;
 font-size: 1.7rem
}

.main-header__list__navbar {
 display: flex;
 align-items: center;
 justify-content: 0;
 font-family: "Poppins", sans-serif
}

.main-header__list__navbar__item {
 padding: .5em 1em;
 cursor: pointer;
 font-size: .7rem;
 color: #000
}

.main-header__list__navbar__item:hover {
 color: #fff;
 background-color: #1a1a1a
}

.main-header__icon {
 width: 164px;
 height: 65px;
 display: grid;
 align-items: center;
 justify-content: center
}

.main-home {
 display: grid;
 align-items: center;
 justify-content: 0;
 height: 70vh
}

.main-home__main {
 color: #292f36;
 display: flex;
 align-items: center;
 justify-content: space-around
}

.main-home__main__text {
 width: 50vw;
 height: 100%;
 gap: 25px;
 display: grid;
 align-items: center;
 justify-content: space-around
}

.main-home__main__text__title {
 font-size: 4.2rem;
 font-family: "Poppins", sans-serif;
 margin: 0 !important;
 color: #292f36
}

.main-home__main__text__info {
 font-size: .8rem;
 font-family: "Inter", sans-serif;
 text-align: justify;
 line-height: 33px
}

.main-home__main__text .button-17 {
 height: 40px;
 font-size: .5rem
}

.main-home__main__img {
 width: 40vw
}

.main-home__main__img img {
 border-radius: 15px;
 box-shadow: rgba(6, 24, 44, .4) 0px 0px 0px 2px, rgba(6, 24, 44, .65) 0px 4px 6px -1px, rgba(255, 255, 255, .08) 0px 1px 0px inset
}

.main-popular .section-title {
 text-align: center;
 color: #292f36
}

.main-popular__div {
 display: flex;
 align-items: center;
 justify-content: space-evenly
}

.main-popular .btn-group {
 width: 100%;
 margin: 6vh 0;
 display: grid;
 align-items: center;
 justify-content: center
}

.main-popular .btn-group button {
 width: 200px;
 height: 65px !important;
 font-size: 1rem
}

.main-what h2 {
 text-align: center;
 color: #292f36
}

.main-what .main-what {
 display: flex;
 align-items: center;
 justify-content: center;
 gap: 25px;
 height: 70vh;
 padding: 0 5em
}

.main-what .main-what__img {
 border-radius: 15px;
 box-shadow: rgba(6, 24, 44, .4) 0px 0px 0px 2px, rgba(6, 24, 44, .65) 0px 4px 6px -1px, rgba(255, 255, 255, .08) 0px 1px 0px inset
}

.main-what .main-what__img img {
 width: 45vw;
 object-fit: contain;
 background-color: #fff !important;
 border-radius: 15px;
 box-shadow: rgba(0, 0, 0, .16) 0px 1px 4px, #333 0px 0px 0px 3px
}

.main-what .main-what__text {
 font-size: .7rem;
 font-family: "Inter", sans-serif;
 text-align: justify;
 line-height: 33px
}

.main-adv {
 margin: 0 2vw;
 display: grid;
 gap: 2vh
}

.main-adv__left {
 display: grid;
 align-items: center;
 justify-content: 0
}

.main-adv__left h3 {
 font-size: 2.5rem;
 font-family: "Poppins", sans-serif;
 color: #292f36
}

.main-adv__left__text {
 gap: 15px;
 display: grid
}

.main-adv__left__text__info {
 margin: 0 .5em
}

.main-adv__left__text__info ul {
 gap: 25px;
 display: grid
}

.main-adv__left__text__info ul li {
 list-style-type: decimal-leading-zero;
 font-family: "Poppins", sans-serif;
 font-weight: 300
}

.main-adv__right {
 display: grid;
 align-items: center;
 justify-content: 0
}

.main-adv__right h3 {
 font-size: 2.5rem;
 font-family: "Poppins", sans-serif;
 color: #292f36
}

.main-adv__right__text {
 gap: 25px;
 display: grid
}

.main-adv__right__text__info {
 margin: 0 .5em
}

.main-adv__right__text__info ul {
 gap: 25px;
 display: grid
}

.main-adv__right__text__info ul li {
 list-style-type: decimal-leading-zero;
 font-family: "Poppins", sans-serif;
 font-weight: 300
}

.bold {
 font-weight: 900;
 font-size: 1.3rem;
 font-family: "Raleway", sans-serif;
 border-bottom: 2px solid #000
}

.main-adv__text__title {
 font-size: 3rem;
 font-family: "Poppins", sans-serif;
 color: #292f36
}

::-webkit-scrollbar {
 width: 0px
}

.main-libary {
 min-height: 100vh;
 height: 100%;
 margin-top: 5vh
}

.main-libary__search {
 display: flex;
 align-items: center;
 justify-content: space-around;
 flex-direction: column
}

.main-libary__search__input {
 display: grid;
 font-family: "Inter", sans-serif;
 width: 30vw;
 height: 50px;
 box-shadow: rgba(0, 0, 0, .16) 0px 1px 4px, #333 0px 0px 0px 3px;
 border-radius: 15px;
 padding: .4em .4em;
 outline: none;
 border: none;
 font-size: 1.2rem
}

.main-libary__search__input:invalid {
 animation: shake both .3s;
 color: red
}

@keyframes shake {
 25% {
  transform: translateX(4px)
 }

 50% {
  transform: translateX(-4px)
 }

 75% {
  transform: translateX(4px)
 }
}

.main-libary__search__list {
 display: none !important;
 display: flex;
 align-items: center;
 justify-content: center
}

.main-libary__search__list div {
 background-color: #fff;
 margin: 0 .5em;
 width: 100%;
 height: 50px;
 border-radius: 15px;
 text-align: center;
 display: grid;
 align-items: center;
 justify-content: center;
 color: purple;
 padding: 0 .7em;
 font-family: "Poppins", sans-serif;
 font-weight: 600;
 box-shadow: rgba(50, 50, 93, .25) 0px 13px 27px -5px, rgba(0, 0, 0, .3) 0px 8px 16px -8px
}

.main-libary__list {
 padding: 5vh 0
}

.main-libary__list__item {
 display: grid;
 align-items: center;
 justify-content: center
}

.main-libary__list__item__items {
 display: grid;
 align-items: center;
 justify-content: center;
 grid-template-columns: auto auto auto
}

.main-libary__list__item__items__div {
 display: grid;
 align-items: center;
 justify-content: center;
 padding: .8em;
 border-radius: 15px;
 background-color: #fff;
 border-bottom: 2px solid #add8e6;
 width: 400px;
 height: 260px;
 margin: 1vw;
 box-shadow: rgba(0, 0, 0, .053) 0px 1px 4px, rgba(51, 51, 51, .52) 0px 0px 0px 3px
}

.main-libary__list__item__items__div:hover,
.main-libary__list__item__items__div:focus {
 box-shadow: rgba(0, 0, 0, .16) 0px 1px 4px, #333 0px 0px 0px 3px
}

.main-libary__list__item__items__div__cat {
 display: flex;
 align-items: center;
 justify-content: 0
}

.main-libary__list__item__items__div__cat h2 {
 padding: .2em .6em;
 background-color: rgba(0, 0, 255, .702);
 font-size: .5rem;
 color: #fff !important;
 border-radius: 5px;
 font-family: "Poppins", sans-serif
}

.main-libary__list__item__items__div__title {
 align-self: flex-start;
 font-size: 1.1rem;
 font-family: "Poppins", sans-serif;
 color: #292f36 !important
}

.main-libary__list__item__items__div__info {
 align-self: flex-start;
 font-weight: 500;
 color: #292f36 !important;
 line-height: 20px;
 display: -webkit-box;
 -webkit-line-clamp: 3;
 -webkit-box-orient: vertical;
 font-size: .5rem;
 overflow: hidden;
 font-family: "Raleway", sans-serif
}

.main-libary__list__item__items__div a {
 font-family: "Raleway", sans-serif;
 font-weight: 600;
 display: flex;
 align-items: center;
 justify-content: 0;
 gap: 10px;
 color: rgba(0, 0, 255, .702) !important
}

.main-libary__list__item__items__div a .move {
 color: rgba(0, 0, 255, .702) !important
}

.main-libary__list__item__items__div a:hover .move {
 transform: translateX(5px)
}

.main-about {
 width: 100vw;
 margin-top: 5vh;
 gap: 25px;
 display: flex;
 align-items: center;
 justify-content: center
}

.main-about__img {
 width: 30vw
}

.main-about__text {
 width: 50vw
}

.main-about__text h3 {
 letter-spacing: 10px;
 border-bottom: 4px solid #8b4513
}

.main-about__text p {
 line-height: 25px
}

.main-contact {
 display: flex;
 align-items: center;
 justify-content: center;
 height: 80vh;
 gap: 25px
}

.main-contact__img img {
 width: 700px;
 border-radius: 15px;
 background-color: #fff !important;
 box-shadow: rgba(6, 24, 44, .4) 0px 0px 0px 2px, rgba(6, 24, 44, .65) 0px 4px 6px -1px, rgba(255, 255, 255, .08) 0px 1px 0px inset
}

.main-contact__form {
 display: grid;
 position: relative;
 gap: 25px
}

.main-contact__form div {
 font-family: "Poppins", sans-serif
}

.main-contact__form input,
.main-contact__form textarea {
 width: 450px;
 height: 50px;
 border: none;
 outline: none;
 border-bottom: 2px solid #000;
 padding: .5em;
 text-transform: capitalize;
 font-family: "Raleway", sans-serif;
 font-size: .8rem
}

.main-contact__form .main-contact__form__input__email {
 text-transform: unset !important
}

.main-contact__form textarea {
 max-width: 450px;
 min-width: 450px;
 height: 105px !important
}

.after-submit {
 display: flex;
 align-items: center;
 justify-content: center;
 flex-direction: column;
 width: 100%;
 background: url(https://blush.design/api/download?shareUri=doIXXJnmK&w=800&h=800&fm=png) no-repeat center;
 background-size: contain;
 height: 100vh
}

.after-submit p {
 font-size: 5.6rem;
 font-weight: 600;
 color: #fff !important;
 font-family: "Poppins", sans-serif !important;
 text-shadow: 2px 10px 15px #000
}

.after-submit button {
 font-size: 1.4rem;
 width: 206px !important;
 height: 70px !important
}

footer {
 margin-top: 5vh;
 background-color: #121212;
 height: auto;
 box-shadow: rgba(0, 0, 0, .2) 0px 60px 40px -7px;
 font-family: "Inter", sans-serif;
 padding-top: 40px;
 color: #fff
}

footer .footer-content {
 display: flex;
 align-items: center;
 justify-content: center;
 flex-direction: column;
 text-align: center
}

footer .footer-content h3 {
 font-size: 2.1rem;
 font-weight: 500;
 text-transform: capitalize;
 font-family: "Poppins", sans-serif !important;
 line-height: 3rem
}

footer .footer-content p {
 max-width: 600px;
 font-family: "Inter", sans-serif;
 margin: 10px auto;
 line-height: 28px;
 font-size: 14px;
 color: #cacdd2
}

footer .socials {
 list-style: none;
 display: flex;
 align-items: center;
 justify-content: center;
 margin: 1rem 0 3rem 0
}

footer .socials li {
 margin: 0 10px
}

footer .socials a {
 text-decoration: none;
 color: #fff;
 border: 1.1px solid #fff;
 padding: 5px;
 border-radius: 50%
}

footer .socials a i {
 font-size: 1.1rem;
 width: 20px;
 transition: color .4s ease
}

footer .socials a:hover i {
 color: aqua
}

footer .footer-bottom {
 background: #000;
 width: 100vw;
 padding: 20px;
 padding-bottom: 40px;
 text-align: center
}

footer .footer-bottom p {
 float: left;
 font-size: 14px;
 word-spacing: 2px;
 text-transform: capitalize
}

footer .footer-bottom p a {
 color: #44bae8;
 font-size: 16px;
 text-decoration: none
}

footer .footer-bottom span {
 text-transform: uppercase;
 opacity: .4;
 font-weight: 200
}

footer .footer-menu {
 float: right
}

footer .footer-menu ul {
 display: flex
}

footer .footer-menu ul li {
 padding-right: 10px;
 display: block
}

footer .footer-menu ul li a {
 color: #cfd2d6;
 text-decoration: none
}

footer .footer-menu ul li a:hover {
 color: #27bcda
}

@media(max-width: 500px) {
 footer .footer-menu ul {
  display: flex;
  margin-top: 10px;
  margin-bottom: 20px
 }
}

@media screen and (max-width: 756px) {
 .bg-screen {
  display: none !important
 }

 .sm-screen {
  display: grid;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh
 }

 .sm-screen h2 {
  font-size: 1.5rem !important;
  text-align: center
 }
}

